// import { Children } from "react"

const Modal = ({children, title="title",  onclick, close}) => {

    return (
        <div className={`modal fade show ${ onclick ? 'd-block' : ''} `} tabIndex="-1" >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h3 className="modal-title">{title}</h3>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={() => close(false)}></button>
                    </div>
                    <div className="modal-body">
                        {children}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Modal